<template>
  <nav class="main-header navbar navbar-expand-lg navbar-white navbar-light">
    <ul class="navbar-nav">
      <li class="nav-item">
        <a class="nav-link" data-widget="pushmenu" href="#" role="button">
          <i class="fas fa-bars"></i>
        </a>
      </li>
    </ul>

    <div class="collapse navbar-collapse" id="navbarSupportedContent">
      <div class="row ml-1 mt-1" style="height: 30px">
        <ul class="breadcrumb float-sm-left bg-transparent justify-content-start">
          <li v-for="(item, idx) in  items " class="breadcrumb-item active" :key="idx">
            <router-link v-if="item.href !== '#'" :to="item.href">
              <i :class="item.icon"></i>
              <b> {{ item.text }}</b>
            </router-link>
            <span v-else :to="item.href">
              <i :class="item.icon"></i>
              <b>&nbsp {{ item.text }}</b>
            </span>
          </li>
        </ul>
      </div>
    </div>

    <ul class="d-inline-flex navbar-nav">
      <div class="d-inline-flex px-1">
        <li class="d-inline-flex px-1 nav-item dropdown">
          <a v-if="!user" class="nav-link" href="/login">
            <i class="far fa-user"></i>
          </a>
          <a v-if="user" class="nav-link" data-toggle="dropdown" href="#">
            <i class="fas fa-user"></i>
          </a>
          <div class="dropdown-menu dropdown-menu-right">
            <a href="/loginedituserview" class="dropdown-item">
              <i class="fa fa-key mr-2"></i> Change Password
            </a>
            <div class="dropdown-divider"></div>

            <a v-if="userlevels >= 50" href="/loginadduserview" class="dropdown-item">
              <i class="fas fa-user mr-2"></i> Add User
            </a>
            <div v-if="userlevels >= 50" class="dropdown-divider"></div>
            <a href="/loginview" @click="Logout()" class="dropdown-item">
              <i class="fas fa-sign-out-alt mr-2"></i> Log Out
            </a>
          </div>
        </li>
        <li class="d-inline-flex px-1 nav-item">
          <a class="nav-link" data-widget="fullscreen" href="#" role="button">
            <i class="fas fa-expand-arrows-alt"></i>
          </a>
        </li>
      </div>
    </ul>

  </nav>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "Navbar",

  props: {
    items: Array,
  },
  data() {
    return {
      userlevels: null,
      userroles: null,
      BDate:
        new Date().getFullYear() +
        "-" +
        (new Date().getMonth() + 1).toString().padStart(2, "0") +
        "-01",
      SDate: new Date().toLocaleDateString("fr-CA"),
      strpage: "Home",
    };
  },

  methods: {
    Logout() {
      localStorage.removeItem("token");
      localStorage.removeItem("roles");
      localStorage.removeItem("first_name");
      this.$store.dispatch("user", null);
    },


    setdate() {
      var m1 = (new Date(new Date()).getMonth() + 1)
        .toString()
        .padStart(2, "0");
      var y1 = new Date().getFullYear();
      var d1 = y1 + "-" + m1 + "-" + "01";
      this.BDate = d1;
      var date01 = new Date(new Date()).getDate();
      var d2 = y1 + "-" + m1 + "-" + date01;
      //console.log(new Date(d2))

      this.$store.dispatch("jamnow", d2);
      this.SDate = new Date(this.$store.state.jamnow).toLocaleDateString(
        "fr-CA"
      );
      //console.log(this.SDate)
    },

  },
  computed: {
    ...mapGetters(["user"]),
  },

  mounted() {
    if (localStorage.getItem("roles") >= 50) {
      this.userroles = "admin";
      this.userlevels = localStorage.getItem("roles");
    } else {
      this.userroles = "operator";
      this.userlevels = localStorage.getItem("roles");
    }
  },

  beforeMount() {
    // this.$store.state.bdate = this.BDate;
    // this.$store.state.jamnow = this.SDate;
  },

  beforeCreate() { },

  updated() {
    // var pagename = this.$store.state.items[0].text;
    // if (pagename.includes("Mixing") || pagename.includes("Quality")) {
    //   this.strpage = "Hide";
    // } else {
    //   this.strpage = "Home";
    // }
  },
};
</script>